@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
.Social{position:absolute;top:0;right:0;display:flex;font-size:1.5em}.Social a{color:inherit;margin:0.5em}

.Wallpaper{z-index:-1;position:absolute;top:0;left:0;width:100%;height:100%}

.LevenshteinMerryGoRound{display:inline-flex;-webkit-transition:width;transition:width;font-family:'Roboto Mono', monospace}.char{display:inline-flex;flex-direction:column;text-align:center;-webkit-transition:width 1s;transition:width 1s}.char .orig,.char .next{width:auto}.char .orig{display:none}.space{visibility:hidden}@-webkit-keyframes widen{0%{height:0em;opacity:0}100%{height:1em;opacity:1}}@keyframes widen{0%{height:0em;opacity:0}100%{height:1em;opacity:1}}

.App{background-color:#FFF;position:fixed;top:0;left:0;width:100vw;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}

